<template>
  <div id="app" :style="appStyle">
    <Happy></Happy>
  </div>
</template>

<script>
import Happy from "@/components/Happy";
import { isPC } from "@/tool/os";
export default {
  name: "App",
  components: {
    Happy
  },
  data() {
    return {
      scale: 1,
      width: 1024,
      height: 768,
      translateX: 0,
      translateY: 0,
      paddingXRatio: 0,
      paddingYRatio: 0
    };
  },
  computed: {
    appStyle() {
      return {
        position: "absolute",
        top: isPC() ? "0" : "210px",
        zIndex: "11",
        width: `${this.width}px`,
        height: `${this.height}px`,
        transformOrigin: "left top",
        transform: `scale(${this.scale})`
      };
    }
  },
  created() {
    this.resize();
    var resizeTimeout = null;
    var vm = this;
    window.addEventListener("resize", function() {
      clearTimeout(resizeTimeout);

      resizeTimeout = setTimeout(vm.resize, 50);
    });

    window.addEventListener("pageshow", function() {
      clearTimeout(resizeTimeout);

      resizeTimeout = setTimeout(vm.resize, 50);
    });
  },
  methods: {
    scaleDOMWrap() {
      let winWidth = window.innerWidth;
      let winHeight = window.innerHeight;
      let standWidth = 1024;
      let standHeight = 768;
      let scaleX = winWidth / standWidth;
      let scaleY = winHeight / standHeight;
      let scale = Math.min(scaleX, scaleY);

      return {
        width: standWidth,
        height: standHeight,
        scale
      };
    },
    resize() {
      let { width, height, scale } = this.scaleDOMWrap();
      this.width = width;
      this.height = height;
      this.scale = scale;
    }
  }
};
</script>

<style>
html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  font-family: "PingFang SC";
  background: rgb(26, 32, 30);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
#app {
  width: 100%;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



