<template>
  <div class="happy">
    <audio controls="controls" muted preload="auto" id="audio">
      <source src="../assets/music/happy.wav" type="audio/ogg" />
      <source src="../assets/music/happy.wav" type="audio/mpeg" />
    </audio>
    <audio controls="controls" muted preload="auto" id="audio1">
      <source src="../assets/music/circulation.wav" type="audio/ogg" />
      <source src="../assets/music/circulation.wav" type="audio/mpeg" />
    </audio>
    <div class="bg-warp" ref="bg-warp">
      <div class="bg bg_1">
        <div class="lll">Y J F</div>
        <div class="action" @click.once="go">
          <div class="text">开 始</div>
        </div>
        <div class="sun"></div>
        <div class="cloud1 cloud"></div>
        <div class="cloud2 cloud"></div>
      </div>
      <div class="bg bg_2">
        <div class="door-warp">
          <div class="left-door" ref="leftDoor"></div>
          <div class="right-door" ref="rightDoor"></div>
        </div>
        <div class="bird" ref="bird"></div>
      </div>
      <div class="bg bg_3">
        <div class="girl" :class="{ 'girl-rotate': girlRotate }"></div>
        <div
          v-for="(item, index) in 4"
          :class="['water', `water${item}`]"
          :key="index + 'water'"
        ></div>
        <div
          v-for="(item, index) in 16"
          :class="['star', `star${item}`]"
          :key="index + 'star'"
        ></div>
        <div v-for="(item, index) in 6" :key="index + 'flowerwarp'">
          <div
            :class="['flower', `flower${item}`]"
            v-for="(i, index) in 30"
            :key="index + 'flower'"
            :ref="`flower${item}`"
            :style="{
              left: `${_random(100, 900)}px`,
              top: `${_random(-6000, -50)}px`,
              transform: `rotate(${_random(0, 360)}deg)`
            }"
          ></div>
        </div>
      </div>
    </div>
    <div class="boy" ref="boy"></div>
    <div class="boy1" ref="boy1"></div>
    <div class="boy2" ref="boy2" :class="{ 'boy-rotate': boyRotate }"></div>
    <div class="end" ref="end">剧 终</div>
  </div>
</template>

<script>
export default {
  name: "Happy",
  data() {
    return {
      girlRotate: false,
      boyRotate: false,
      timer: null
    };
  },
  created() {},
  mounted() {
    //     this.flowerFly();
  },
  methods: {
    _random(min, max) {
      return Math.floor(Math.random() * (max - min)) + min;
    },
    playAudio1() {
      let audio = document.getElementById("audio");
      audio.play();
    },
    playAudio2() {
      let audio = document.getElementById("audio1");
      audio.play();
    },

    go() {
      this.playAudio1();
      this.$anime({
        targets: this.$refs["boy"],
        left: 600,
        duration: 4500,
        easing: "linear",
        complete: () => {
          this.moveBg();
        }
      });
    },
    go2() {
      this.$anime({
        targets: this.$refs["boy1"],
        left: 162,
        duration: 2000,
        easing: "linear",
        complete: () => {}
      });
    },
    go3() {
      this.$anime({
        targets: this.$refs["boy1"],
        left: 300,
        top: 300,
        duration: 2000,
        easing: "linear",
        complete: () => {
          this.go4();
        }
      });
    },
    go4() {
      this.$anime({
        targets: this.$refs["boy1"],
        left: 406,
        duration: 2000,
        easing: "linear",
        complete: () => {
          this.$refs["boy1"].style.animationPlayState = "paused";
          this.$refs["boy1"].style.opacity = 0;
          this.$refs["boy2"].style.opacity = 1;
          setTimeout(() => {
            this.rotate();
          }, 2000);
        }
      });
    },
    moveBg2() {
      this.$anime({
        targets: this.$refs["bg-warp"],
        translateX: "-2048px",
        duration: 6000,
        easing: "linear",
        complete: () => {
          this.go3();
        }
      });
    },
    closeDoor() {
      this.$anime({
        targets: this.$refs.leftDoor,
        left: [-65, 0],
        duration: 1000,
        easing: "linear",
        complete: () => {}
      });
      this.$anime({
        targets: this.$refs.rightDoor,
        left: [130, 65],
        duration: 1000,
        easing: "linear",
        complete: () => {}
      });
    },
    openDoor() {
      this.$anime({
        targets: this.$refs.leftDoor,
        left: -65,
        duration: 1000,
        easing: "linear",
        complete: () => {}
      });
      this.$anime({
        targets: this.$refs.rightDoor,
        left: 130,
        duration: 1000,
        easing: "linear",
        complete: () => {
          this.comeIn();
        }
      });
    },
    moveBg() {
      this.$anime({
        targets: this.$refs["bg-warp"],
        translateX: "-926px",
        duration: 6000,
        easing: "linear",
        complete: () => {
          this.birdFly();
          this.openDoor();
        }
      });
    },
    comeIn() {
      this.$anime({
        targets: this.$refs["boy"],
        // left: 700,
        top: 350,
        opacity: 0,
        duration: 2000,
        easing: "linear",
        complete: () => {
          setTimeout(() => {
            this.goOut();
          }, 1000);
        }
      });
    },
    goOut() {
      this.$anime({
        targets: this.$refs["boy1"],
        top: [350, 390],
        opacity: [0, 1],
        duration: 2000,
        easing: "linear",
        complete: () => {
          this.closeDoor();
          this.go2();
          this.moveBg2();
        }
      });
    },
    rotate() {
      this.girlRotate = true;
      this.boyRotate = true;
      this.flowerFly();
      let end = this.$refs.end;
      setTimeout(() => {
        this.playAudio2();
        setTimeout(() => {
          end.style.opacity = 1;
          this.$anime({
            targets: end,
            top: [-300, 200],
            duration: 3000,
            easing: "easeInOutSine",
            complete: () => {}
          });
        }, 30000);
        setTimeout(() => {
          clearInterval(this.timer);
        }, 57000);
      }, 2000);
    },
    birdFly() {
      this.$refs.bird.style.opacity = 1;
      this.$anime({
        targets: this.$refs.bird,
        left: [1024, 290],
        duration: 6000,
        easing: "linear",
        complete: () => {}
      });
    },
    flowerFly() {
      this.timer = setInterval(() => {
        for (let i = 0; i < 6; i++) {
          this.$refs[`flower${i + 1}`].forEach(item => {
            item.style.top = parseFloat(item.style.top) + 3 + "px";
          });
        }
      }, 50);
    }
  }
};
</script>

